
.button-up {
  z-index: 200;
  position: fixed;
  right: 40px;
  bottom: 40px;
  width: 66px;
  height: 66px;
  padding: 0;
  box-shadow: 0 0 10px rgb(0, 0, 0, 25%);
  background-image: url("/build/images/arrowUp.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 27px;
  background-color: #468217;
  border-radius: 100%;
  cursor: pointer;
  outline: none;
  border: none;
  transition: opacity 200ms, transform 0.6s ease;

  &:active {
    transform: translateY(3px);
  }

  &.show {
    opacity: 1;
  }

  &.hide {
    opacity: 0;
  }

  @media (max-width: 992px) {
    right: 15px;
    bottom: 25px;
    width: 45px;
    height: 45px;
    background-size: 19px;
  }
}
